/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}


/* 프로젝트 콘텐츠 */
.wrapper{
    position: absolute;
    width: 1100px;
    height: 68%;
    top: 25%;
    left: 50%;
    margin-left: -550px;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    animation: fadeInWrapper 0.8s forwards;
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}


/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }
    .wrapper{
        width: 800px;
        height: 72%;
        margin-left: -400px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 0;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:22%; }
    }
    .menu{
        text-align: center;
        cursor: pointer;
        transition: 0.5s;
    }
    .menu > span{
        content: '';
        position: absolute;
        right: 0;
        top: 7px;
        width: 1px;
        height: 20px;
        background: gray;
    }
    .scroll{
        grid-column-start: 1;
        grid-column-end: 5;
    }
    .content{
        grid-column-start: 1;
        grid-column-end: 5;
    }
    .content .per{
        width: 142px;
        height: 142px;
    }
}

