/* 프로젝트 콘텐츠 */
.wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 35px 8px 1fr;
    grid-column-gap: 70px;
    position: absolute;
    width: 1040px;
    height: calc(100% - 60px);
    padding: 30px;
    border-radius: 20px;
}

/* 메뉴 */
.menu{
    line-height: 35px;
    font-size: 22px;
    font-weight: bold;
    -webkit-animation: fadeInMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: fadeInMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@-webkit-keyframes fadeInMenu {
    0% {
        -webkit-transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
        opacity: 1;
    }
}
@keyframes fadeInMenu {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

/* 스크롤바 */
.scroll{
    height: 3px;
    background: rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInScrollbar 0.5s 0.6s forwards;
}
.scroll > span{
    content: '';
    height: 3px;
    position: absolute;
    background: black;
}
@keyframes fadeInScrollbar{ 100%{ opacity:1; } }

/* 프로젝트 콘텐츠 */
.content{
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    overflow: auto;
}
.content::-webkit-scrollbar{ display: none; }
.content .per{
    width: 132px;
    height: 132px;
    margin: 8px;
    border: 1px solid white;
    border-radius: 10px;
    background: skyblue;
    -webkit-animation: fadeInContent 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: fadeInContent 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.content .per:hover{
    border: 1px solid black;
}
@-webkit-keyframes fadeInContent {
    0% {
        -webkit-transform: scaleY(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: scaleY(1);
        opacity: 1;
    }
}
@keyframes fadeInContent {
    0% {
        transform: scaleY(0);
        opacity: 1;
    }
    100% {
        transform: scaleY(1);
        opacity: 1;
    }
}