/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}


/* 페이지 콘텐츠 */
.wrapper{
    display: grid;
    grid-template-columns: 1.1fr 0.82fr 1.1fr;
    grid-template-rows: 35px 8px 1fr;
    position: absolute;
    width: 1100px;
    height: 58%;
    top: 25%;
    left: 50%;
    margin-left: -550px;
    padding: 30px 0;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    animation: fadeInWrapper 0.8s forwards;
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}

/* 콘텐츠 메뉴 */
.menu{
    margin-left: 40px;
    font-size: 22px;
    font-weight: bold;
    -webkit-animation: fadeInMenu 0.4s 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: fadeInMenu 0.4s 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@-webkit-keyframes fadeInMenu {
    0% {
        -webkit-transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
        opacity: 1;
    }
}
@keyframes fadeInMenu {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

/* 콘텐츠 스크롤바 */
.scroll{
    height: 3px;
    margin-left: 40px;
    background: rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInScrollbar 0.5s 1.6s forwards;
}
.scroll > span{
    content: '';
    height: 3px;
    position: absolute;
    background: black;
}
@keyframes fadeInScrollbar{ 100%{ opacity:1; } }

/* 콘텐츠 */
.content{
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    margin-left: 40px;
    overflow: auto;
}
.content::-webkit-scrollbar{ display: none; }
.content .per_skill{
    width: 70px;
    height: 70px;
    margin: 8px;
    border: 2px solid rgb(204, 204, 204);
    border-radius: 10px;
    opacity: 0;
    transition: 0.3s;
    -webkit-animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
	animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
}
.content .per_skill > svg{transition:0.3s;}
.content .per_skill .skillname{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}
.content .per_skill .skillname > div{
    position: absolute;
    padding: 3px 5px;
    font-size: 14px;
    font-weight: bold;
    line-height: 130%;
    border-radius: 30px;
    background: rgb(239, 239, 239);
    transition: 0.3s;
    cursor: default;
    opacity: 0;
}
.content .per_skill:hover{border:2px solid black;}
.content .per_skill:hover > svg{filter:blur(2px);}
.content .per_skill:hover > .skillname > div{opacity:1;}
.content .per_cert{
    width: 100%;
    height: 60px;
    margin: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid rgb(204, 204, 204);
    border-radius: 10px;
    background: white;
    transition: 0.3s;
    -webkit-animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
	animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
}
.content .per_cert:hover{border:2px solid black;}
@-webkit-keyframes fadeInContent {
    0% {
        -webkit-transform: scaleY(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: scaleY(1);
        opacity: 1;
    }
}
@keyframes fadeInContent {
    0% {
        transform: scaleY(0);
        opacity: 1;
    }
    100% {
        transform: scaleY(1);
        opacity: 1;
    }
}

/* 정보 표시 디스플레이 */
.info{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column: 3;
    width: 80%;
    height: calc(90% - 6px);
    margin-top: calc(5% - 3px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    border: 3px solid rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInInfo 1s 1.6s forwards;
}
@keyframes fadeInInfo{
    100%{ opacity:1; }
}
.info > .info_skill, .info_cert{
    width: 250px;
    height: 300px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.1fr 0.4fr 0.5fr;
    justify-items: center;
    align-items: center;
}
.info > .info_skill > div:nth-child(1){
    font-size: 20px;
    font-weight: bold;
    text-decoration: underline;
}
.info > .info_skill > div:nth-child(2){
    width: 100px;
    height: 100px;
    border: 2px solid black;
    border-radius: 10px;
    background: white;
}




/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }
    .wrapper{
        grid-template-columns: 1.35fr 0.65fr;
        grid-template-rows: 28px 5px 1.2fr 1fr;
        width: 800px;
        height: 63%;
        top: 22%;
        margin-left: -430px;
        padding: 30px;
        grid-column-gap: 60px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:22%; }
    }
    .menu{ margin-left: 0; font-size:18px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:79px; height:79px;}
    .content .per_skill .skillname > div{font-size:15px;}
    .content .per_cert{ height: 60px;}
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
        margin-top: calc(3% - 3px);
    }
    .info > .info_skill{
        width: 80%;
        height: 80%;
        grid-template-columns: 0.3fr 0.7fr;
        grid-template-rows: 0.2fr 0.8fr;
    }
    .info > .info_skill > div:nth-child(1){ font-size:17px;}
    .info > .info_skill > div:nth-child(2){
        width: 80px;
        height: 80px;
        grid-column: 1;
        grid-row: 2;
    }
    .info > .info_skill > div:nth-child(3){
        grid-column: 2;
        grid-row-start: 1;
        grid-row-end: 3;
    }
}

@media screen and (max-width:940px){
    .text_line{ font-size:60px;}
    @keyframes textSize{
        0%{ font-size:60px; }
        100%{ font-size:48px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 25px 5px 1.2fr 0.8fr;
        width: 500px;
        height: 68%;
        margin-left: -280px;
        top: 18%;
        grid-column-gap: 50px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:18%; }
    }
    .menu{ margin-left: 0; font-size: 17px; }
    .scroll{ margin-left:0; height:2.2px; }
    .scroll > span{ height:2.2px; }
    .content{ margin-left:0; }
    .content .per_skill{ width:68px; height:68px; margin:5px; }
    .content .per_skill .skillname > div{font-size:14px;}
    .content .per_cert{ height: 50px; margin:5px; }
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }
    .info > .info_skill > div:nth-child(1){ font-size:15px;}
    .info > .info_skill > div:nth-child(2){ width: 60px; height: 60px; }
    .info > .info_skill > div:nth-child(3){ font-size: 13px;}
}

@media screen and (max-width:620px){
    .text_line{ font-size:50px;}
    @keyframes textSize{
        0%{ font-size:50px; }
        100%{ font-size:40px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 22px 5px 1.2fr 0.8fr;
        width: 280px;
        height: 67%;
        margin-left: -170px;
        top: 18%;
        grid-column-gap: 30px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:18%; }
    }
    .menu{ margin-left: 0; font-size: 15px; }
    .scroll{ margin-left:0; height:1.8px; }
    .scroll > span{ height:1.8px; }
    .content{ margin-left:0; }
    .content .per_skill{ width:57.5px; height:57.5px; margin:3px;}
    .content .per_skill .skillname > div{font-size:13px;}
    .content .per_cert{ height: 43px; margin:3px; }
    .content .per_cert > div{font-size:12px;}
    .info > .info_skill > div:nth-child(1){ font-size:13px;}
    .info > .info_skill > div:nth-child(2){ width: 50px; height: 50px; }
    .info > .info_skill > div:nth-child(3){ font-size: 11px;}
}