/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}


.wrapper{
    width: 400px;
    height: 375px;
    padding: 30px 80px;
    position: absolute;
    top: 28%;
    left: 50%;
    margin-left: -280px;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    animation: fadeInWrapper 0.8s forwards;
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}

/* Contact 페이지 메일 및 연결 정보 */
.form{
    width: 350px;
    height: 380px;
}
.form #info{
    display: grid;
    grid-template-columns: 120px 230px;
    margin-top: 40px;
}
.form #info div{
    width: 50px;
    height: 50px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.form #info div img{
    width: 50px;
}
.form div p{
    font-size: 18px;
    font-family: 'simplehae';
}
.form #info #link{
    text-decoration: underline;
    color: blue;
}
.form #info #link:hover{
    color: gray;
}



/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }

    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:22%; }
    }
    
}

@media screen and (max-width:880px){
    .text_line{ font-size:50px;}
    @keyframes textSize{
        0%{ font-size:50px; }
        100%{ font-size:35px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }

    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:18%; }
    }


}

@media screen and (max-width:580px){
    .text_line{ font-size:40px;}
    @keyframes textSize{
        0%{ font-size:40px; }
        100%{ font-size:28px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-43%; }
    }

    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:14%; }
    }

    
}
