/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}



.wrapper{
    width: 900px;
    height: 60%;
    position: absolute;
    left: 50%;
    top: 25%;
    margin-left: -480px;
    padding: 30px;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    animation: fadeInWrapper 0.8s forwards;
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}


/* PROFILE 페이지 나의 소개 영역 */
.me{
    display: grid;
    grid-template-columns: 300px 1fr;
    align-items: center;
    justify-items: center;
}
.me #photo{                /* 프로필 이미지 */
    width: 160px;
    height: 210px;
    border: 1px solid gray;
    border-radius: 10px;
}
.me #intro{ width:100%; height:180px; }
.me #intro > p{
    font-size:20px;
    font-family: 'simplehae';
}
.me #intro > div{
    width: 80%;
    padding: 20px 30px;
    font-size: 22px;
    font-family: 'shiningstar';
    line-height: 28px;
    border-left: 3px solid gray;
}


/* PROFILE 페이지 경험 영역 */
.exp{
    width: 100%;
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
}
.exp > div{
    width: 240px;
    font-family: 'simplehae';
    color: rgb(78, 78, 78);
}
.exp > div > div{
    color: black;
    font-weight: bold;
    font-size: 18px;
}
.exp > div > div:before{
    content: '';
    height: 5px;
    position: absolute;
    bottom: 0;
    background: black;
}




/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }
    .wrapper{
        width: 800px;
        height: 65%;
        top: 20%;
        margin-left: -430px;
        padding: 30px;
        grid-column-gap: 60px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:20%; }
    }
}

@media screen and (max-width:880px){
    .text_line{ font-size:50px;}
    @keyframes textSize{
        0%{ font-size:50px; }
        100%{ font-size:35px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }
    .wrapper{
        width: 500px;
        height: 70%;
        margin-left: -280px;
        top: 18%;
        padding: 0 30px;
        grid-column-gap: 50px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:18%; }
    }
    .me{ grid-template-columns: 200px 300px; }
    .me #photo{                /* 프로필 이미지 */
        width: 120px;
        height: 160px;
        border: 1px solid gray;
        border-radius: 10px;
    }
    .me #intro{ height:150px; }
    .me #intro > p{
        font-size:16px;
        font-family: 'simplehae';
    }
    .me #intro > div{
        width: 250px;
        padding: 2px 15px;
        font-size: 16px;
        font-family: 'shiningstar';
        line-height: 22px;
        border-left: 3px solid gray;
    }
    .exp{ margin-top:10px; }
    .exp > div{
        width: 150px;
        font-size: 13px;
    }
    .exp > div > div{
        font-size: 15px;
    }
}

@media screen and (max-width:580px){
    .text_line{ font-size:40px;}
    @keyframes textSize{
        0%{ font-size:40px; }
        100%{ font-size:28px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-43%; }
    }
    .wrapper{
        width: 350px;
        height: 75%;
        margin-left: -205px;
        top: 12%;
        grid-column-gap: 30px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:12%; }
    }
    .me{ grid-template-columns:120px 260px; }
    .me #photo{                /* 프로필 이미지 */
        width: 80px;
        height: 110px;
        border: 1px solid gray;
        border-radius: 10px;
    }
    .me #intro{ height:120px; }
    .me #intro > p{
        font-size:14px;
        font-family: 'simplehae';
    }
    .me #intro > div{
        width: 220px;
        padding: 2px 15px;
        font-size: 13px;
        font-family: 'shiningstar';
        line-height: 22px;
        border-left: 3px solid gray;
    }
    .exp{ margin-top:0; }
    .exp > div{
        width: 100px;
        font-size: 12px;
    }
    .exp > div > div{
        font-size: 14px;
    }
}