.circle{
    position: fixed;
    border-radius: 1000px;
    opacity: 0.6;
    z-index: -1;
}

.circle:nth-child(1){width:300px; height:300px; left:-2%; bottom:-5%; background:greenyellow;}
.circle:nth-child(2){width:200px; height:200px; left:12%; bottom:10%; background:rgb(145, 255, 178);}
.circle:nth-child(3){width:220px; height:220px; left:15%; top:-5%; background:rgb(250, 185, 255);}
.circle:nth-child(4){width:300px; height:300px; right:8%; top:5%; background:rgb(138, 184, 252);}
.circle:nth-child(5){width:100px; height:100px; right:40%; top:60%; background:rgb(138, 206, 252);}
.circle:nth-child(6){width:120px; height:120px; right:35%; top:65%; background:rgb(138, 184, 252);}